import { Nav, ProgressBar } from "react-bootstrap"
import { LinkContainer } from "react-router-bootstrap"


function CheckoutSteps({step1, step2, step3, step4,}) {
  return (

    <div>
        <ProgressBar variant="primary"  now={step4 ? 100 : step3 ? 65 : step2 ? 40 : step1 ? 20 : 0}
        className="my-3" 
        style={{marginRight: "30px"}}/>
    <Nav className="justify-content-center mb-4">

    <Nav.Item>
        { step1 ? (

            <LinkContainer  to='/login'>
        <Nav.Link  >
            Login
           
        </Nav.Link>
        </LinkContainer>
            ) : <Nav.Link disabled >
            Login
        </Nav.Link>

        }
        
    </Nav.Item>
    <Nav.Item>
        { step2 ? (

            <LinkContainer to='/shipping'>
        <Nav.Link>
        Shipping
        </Nav.Link>
        </LinkContainer>
            ) : <Nav.Link disabled >
            Shipping
        </Nav.Link>

        }
        
    </Nav.Item>

    <Nav.Item>
        { step3 ? (

            <LinkContainer to='/payment'>
        <Nav.Link>
        Payment
        </Nav.Link>
        </LinkContainer>
            ) : <Nav.Link disabled >
            Payment
        </Nav.Link>

        }
        
    </Nav.Item>

    <Nav.Item>
        { step4 ? (

            <LinkContainer to='/placeorder'>
        <Nav.Link>
            Place Order
        </Nav.Link>
        </LinkContainer>
            ) : <Nav.Link disabled >
            Place Order
        </Nav.Link>

        }
        
    </Nav.Item>
    </Nav>
    </div>
  )
}

export default CheckoutSteps